/* MainPage Modern CSS */
:root {
    --mp-primary: #2563eb;
    --mp-success: #10b981;
    --mp-warning: #f59e0b;
    --mp-danger: #ef4444;
    --mp-bg: #f8fafc;
    --mp-border: #e2e8f0;
    --mp-text-dark: #1e293b;
    --mp-text-sub: #64748b;
}

#mp-page-wrapper {
    background-color: var(--mp-bg);
    min-height: 100vh;
    padding: 32px 20px;
    font-family: 'Pretendard', -apple-system, sans-serif;
}

.mp-container { max-width: 1200px; margin: 0 auto; }

/* Header */
.mp-dashboard-header { margin-bottom: 32px; }
.mp-welcome-title { font-size: 26px; font-weight: 800; color: var(--mp-text-dark); margin-bottom: 6px; }
.mp-welcome-subtitle { color: var(--mp-text-sub); font-size: 15px; }

/* Grid */
.mp-content-grid { display: grid; grid-template-columns: 1fr 380px; gap: 24px; }

/* Card Common */
.mp-card {
    background: #fff;
    border-radius: 16px;
    border: 1px solid var(--mp-border);
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.05);
    padding: 24px;
    margin-bottom: 24px;
}

.mp-card-title {
    font-size: 17px; font-weight: 700; color: var(--mp-text-dark);
    display: flex; align-items: center; gap: 8px; margin-bottom: 20px;
}

/* User Info Grid */
.mp-info-grid { display: grid; grid-template-columns: repeat(auto-fit, minmax(200px, 1fr)); gap: 16px; }
.mp-info-item {
    display: flex; align-items: center; gap: 12px; padding: 12px;
    background: #f1f5f9; border-radius: 12px; position: relative;
}
.mp-info-item.missing { background: #fff1f2; border: 1px solid #fecdd3; }
.info-icon-circle {
    width: 36px; height: 36px; border-radius: 50%; background: #fff;
    display: flex; align-items: center; justify-content: center; color: var(--mp-primary);
}
.info-content .label { display: block; font-size: 11px; color: var(--mp-text-sub); font-weight: 600; }
.info-content .value { font-size: 14px; font-weight: 700; color: var(--mp-text-dark); }
.error-dot { position: absolute; top: 8px; right: 8px; color: var(--mp-danger); font-weight: 900; }

/* Alert Card */
.mp-alert-card {
    background: #fffbeb; border: 1px solid #fef3c7; border-radius: 12px;
    padding: 16px; display: flex; gap: 16px; align-items: center; margin-bottom: 24px;
}
.mp-alert-icon { color: var(--mp-warning); }
.mp-alert-title { font-size: 15px; font-weight: 700; margin-bottom: 2px; }
.mp-alert-msg { font-size: 13px; color: #92400e; }
.mp-alert-btn {
    margin-left: auto; padding: 6px 12px; background: var(--mp-warning);
    color: #fff; border: none; border-radius: 6px; font-size: 12px; font-weight: 700; cursor: pointer;
}

/* Vacation Section */
.mp-vacation-header { display: flex; justify-content: space-between; align-items: center; }
.mp-stats-row { display: flex; gap: 12px; margin-bottom: 24px; }
.mp-stat-box { flex: 1; text-align: center; padding: 16px; background: #f8fafc; border-radius: 12px; border: 1px solid var(--mp-border); }
.mp-stat-box.highlight { background: #eff6ff; border-color: #bfdbfe; }
.mp-stat-label { font-size: 12px; color: var(--mp-text-sub); display: block; margin-bottom: 4px; font-weight: 600; }
.mp-stat-value { font-size: 22px; font-weight: 800; }

.mp-progress-section { margin-bottom: 24px; }
.progress-labels { display: flex; justify-content: space-between; font-size: 13px; margin-bottom: 8px; }
.progress-bar-bg { height: 10px; background: #e2e8f0; border-radius: 5px; overflow: hidden; }
.progress-bar-fill { height: 100%; background: var(--mp-primary); border-radius: 5px; transition: width 0.6s cubic-bezier(0.4, 0, 0.2, 1); }

/* History List */
.history-header { display: flex; justify-content: space-between; align-items: center; margin-bottom: 12px; }
.history-title { font-size: 14px; font-weight: 700; }
.history-header button { background: none; border: none; color: var(--mp-primary); font-size: 12px; font-weight: 700; cursor: pointer; }
.history-item {
    display: flex; justify-content: space-between; align-items: center;
    padding: 10px 0; border-bottom: 1px solid #f1f5f9; font-size: 13px;
}
.h-date { color: var(--mp-text-dark); }
.h-days { font-weight: 700; }
.h-status { font-size: 11px; padding: 2px 6px; border-radius: 4px; background: #f1f5f9; }
.h-status.APPROVED { background: #ecfdf5; color: #059669; }

/* Quick Action Grid */
.mp-action-grid { display: grid; grid-template-columns: 1fr 1fr; gap: 10px; }
.mp-action-btn {
    display: flex; flex-direction: column; align-items: center; gap: 8px; padding: 16px;
    border: 1px solid var(--mp-border); border-radius: 12px; background: #fff; cursor: pointer;
    font-size: 13px; font-weight: 600; transition: all 0.2s;
}
.mp-action-btn:hover { border-color: var(--mp-primary); background: #f0f7ff; transform: translateY(-2px); }
.mp-action-btn.primary { background: #f0f7ff; color: var(--mp-primary); border-color: #bfdbfe; }

/* Activity List */
.activity-item { display: flex; gap: 12px; margin-bottom: 16px; position: relative; }
.activity-dot { width: 8px; height: 8px; border-radius: 50%; background: #cbd5e1; margin-top: 6px; }
.activity-item.vacation .activity-dot { background: var(--mp-success); }
.activity-info { display: flex; flex-direction: column; }
.act-title { font-size: 14px; font-weight: 700; }
.act-desc { font-size: 12px; color: var(--mp-text-sub); }
.act-time { font-size: 11px; color: #94a3b8; margin-top: 2px; }

.mp-refresh-btn {
    background: none;
    border: none;
    cursor: pointer;
    padding: 6px;
    border-radius: 6px;
    transition: all 0.2s;
    color: var(--text-secondary);
}

.mp-refresh-btn:hover:not(:disabled) {
    background: var(--bg-light);
    color: var(--primary-blue);
}

.mp-refresh-btn:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

/*    */
.fw-bold { font-weight: 700; }

/*      */
.info-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2px;
}

/*     */
.mp-alert-body {
    flex: 1;
}

/*      (  ) */
.mp-recent-history {
    margin-top: 24px;
    padding-top: 20px;
    border-top: 1px dashed var(--mp-border);
}

/*    */
.mp-activity-list {
    display: flex;
    flex-direction: column;
}

/*    */
.h-status.PENDING { background: #fef3c7; color: #d97706; }   /*  */
.h-status.REJECTED { background: #fee2e2; color: #dc2626; }  /*  */
.h-status.CANCELED { background: #f1f5f9; color: #64748b; }  /*  */

/*     */
.activity-item.login .activity-dot { background: var(--mp-primary); }
.activity-item.vacation .activity-dot { background: var(--mp-success); }
.activity-item.report .activity-dot { background: var(--mp-warning); }

/*    (    ) */
.mp-activity-list { position: relative; }
.activity-item:not(:last-child)::after {
    content: '';
    position: absolute;
    left: 3px;
    top: 20px;
    width: 2px;
    height: calc(100% - 12px);
    background: #e2e8f0;
}

/*    ( ) */
.mp-card {
    transition: transform 0.2s, box-shadow 0.2s;
}
.mp-card:hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

/* New Loading UI */
.mp-loading-container .loading {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 80px 40px;
    text-align: center;
    color: #6b7280;
    font-size: 16px;
}

/*     */

.mp-schedule-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 12px;
}

.month-selector {
    padding: 4px 8px;
    border: 1px solid var(--mp-border);
    border-radius: 6px;
    font-size: 13px;
    font-weight: 600;
    color: var(--mp-text-dark);
    background-color: #fff;
    cursor: pointer;
}

.schedule-stats {
    display: flex;
    gap: 12px;
    margin-bottom: 12px;
    padding: 10px 14px;
    background: #f8fafc;
    border-radius: 8px;
    font-size: 12px;
    font-weight: 600;
    color: var(--mp-text-sub);
    flex-wrap: wrap;
}

/*   */
.work-calendar {
    border: 1px solid var(--mp-border);
    border-radius: 12px;
    overflow: hidden; /*    */
    background: #fff;
    box-shadow: 0 1px 2px rgba(0,0,0,0.05);
}

.calendar-wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
}

/*   (Week)    */
/* : minmax(0, 1fr)       */
.calendar-header,
.calendar-week {
    display: grid;
    grid-template-columns: repeat(7, minmax(0, 1fr));
    width: 100%;
}

.calendar-header {
    background: #f1f5f9;
    border-bottom: 1px solid var(--mp-border);
}

.day-label {
    padding: 8px 0;
    text-align: center;
    font-size: 12px;
    font-weight: 700;
    color: var(--mp-text-dark);
}

/*   */
.day-label.sun, .day-number.sun { color: var(--mp-danger); } /*   */
.day-label.sat, .day-number.sat { color: var(--mp-primary); } /*   */

.calendar-week {
    border-bottom: 1px solid #f1f5f9;
}
.calendar-week:last-child {
    border-bottom: none;
}

/*    */
.calendar-day {
    position: relative;
    /* aspect-ratio ,   min-height  */
    min-height: 55px;
    padding: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 2px;
    border-right: 1px solid #f8fafc; /*    */
    background: white;
}

.calendar-day:nth-child(7) {
    border-right: none;
}

.calendar-day.empty {
    background: #fafafa;
}

/*   */
.day-number {
    font-size: 12px;
    font-weight: 500;
    color: #94a3b8;
    margin-bottom: 2px;
}

/*    */
.work-type {
    font-size: 11px;
    font-weight: 700;
    padding: 2px 6px;
    border-radius: 4px;
    text-align: center;
    width: 100%; /*     */
    max-width: 40px; /*     */
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.2;
}

/*     */
/* Day: /  */
.work-type.type-d {
    background: #fff7ed;
    color: #c2410c;
    border: 1px solid #ffedd5;
}

/* Evening: /  ( Day ) */
.work-type.type-e {
    background: #fdf2f8;
    color: #db2777;
    border: 1px solid #fce7f3;
}

/* Night:  /  */
.work-type.type-n {
    background: #eef2ff;
    color: #4338ca;
    border: 1px solid #e0e7ff;
}

/* Off: /  */
.work-type.type-off {
    background: #f1f5f9;
    color: #64748b;
    font-weight: 500;
}

/* Vacation:   ( ) */
.work-type.type-vacation {
    background: #fef2f2;
    color: #dc2626;
    border: 1px solid #fee2e2;
}

/* Half:   */
.work-type.type-half {
    background: #ecfdf5;
    color: #059669;
    border: 1px solid #d1fae5;
}

/*  */
.work-type.type-etc {
    background: #f8fafc;
    color: #475569;
}

/*   (   ) */
@media (max-width: 480px) {
    .work-type {
        font-size: 10px;
        padding: 1px 2px;
    }
    .calendar-day {
        min-height: 45px;
    }
}

/*    */
@media (max-width: 768px) {
    #mp-page-wrapper {
        padding: 20px 12px;
    }
    .mp-welcome-title {
        font-size: 22px;
    }
    .mp-info-grid {
        grid-template-columns: 1fr; /*    */
    }
    .mp-stats-row {
        flex-wrap: wrap;
    }
    .mp-stat-box {
        min-width: calc(50% - 6px); /*   2 */
    }
}

/* Utilities */
.spinning { animation: spin 1s linear infinite; }
@keyframes spin { from { transform: rotate(0deg); } to { transform: rotate(360deg); } }
.text-green { color: var(--mp-success); }
.icon-blue { color: var(--mp-primary); }
.icon-green { color: var(--mp-success); }
.icon-orange { color: var(--mp-warning); }
.inner-loader { padding: 20px; text-align: center; color: var(--mp-text-sub); font-size: 13px; }
.empty-msg { padding: 20px; text-align: center; color: var(--mp-text-sub); font-size: 13px; }

@media (max-width: 1024px) { .mp-content-grid { grid-template-columns: 1fr; } }