/* ==========================================
   Admin Dashboard - Enhanced UI Styles
   ========================================== */

/*    */
:root {
    /* Primary Colors */
    --primary-600: #4F46E5;
    --primary-700: #4338CA;
    --primary-50: #EEF2FF;

    /* Secondary Colors */
    --secondary-600: #06B6D4;
    --secondary-700: #0891B2;

    /* Status Colors */
    --success-500: #10B981;
    --success-600: #059669;
    --warning-500: #F59E0B;
    --warning-600: #D97706;
    --danger-500: #EF4444;
    --danger-600: #DC2626;
    --info-500: #3B82F6;

    /* Neutral Colors */
    --gray-50: #F9FAFB;
    --gray-100: #F3F4F6;
    --gray-200: #E5E7EB;
    --gray-300: #D1D5DB;
    --gray-400: #9CA3AF;
    --gray-500: #6B7280;
    --gray-600: #4B5563;
    --gray-700: #374151;
    --gray-800: #1F2937;
    --gray-900: #111827;

    /* Spacing */
    --spacing-xs: 0.5rem;
    --spacing-sm: 0.75rem;
    --spacing-md: 1rem;
    --spacing-lg: 1.5rem;
    --spacing-xl: 2rem;

    /* Border Radius */
    --radius-sm: 0.375rem;
    --radius-md: 0.5rem;
    --radius-lg: 0.75rem;
    --radius-xl: 1rem;

    /* Shadows */
    --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
    --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

/* ==========================================
    
   ========================================== */
.admin-dashboard-container {
    padding: var(--spacing-xl);
    background: linear-gradient(to bottom, var(--gray-50) 0%, #ffffff 100%);
    min-height: 100vh;
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
}

/* ==========================================
    
   ========================================== */
.admin-dashboard-title {
    font-size: 2rem;
    font-weight: 700;
    background: linear-gradient(135deg, var(--primary-600) 0%, var(--secondary-600) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin-bottom: var(--spacing-sm);
    letter-spacing: -0.025em;
}

.admin-welcome-message {
    color: var(--gray-600);
    font-size: 0.9375rem;
    margin-bottom: var(--spacing-xl);
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
}

/* ==========================================
     (  )
   ========================================== */
.admin-stats-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: var(--spacing-lg);
    margin-bottom: var(--spacing-xl);
}

.admin-stat-card {
    background: white;
    border-radius: var(--radius-lg);
    padding: var(--spacing-lg);
    box-shadow: var(--shadow-sm);
    border: 1px solid var(--gray-200);
    transition: all 0.3s ease;
    position: relative;
    overflow: hidden;
}

.admin-stat-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(90deg, var(--primary-600), var(--secondary-600));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.admin-stat-card:hover {
    transform: translateY(-4px);
    box-shadow: var(--shadow-lg);
}

.admin-stat-card:hover::before {
    transform: scaleX(1);
}

.admin-stat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--spacing-md);
}

.admin-stat-title {
    font-size: 0.875rem;
    color: var(--gray-600);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.05em;
}

.admin-stat-icon {
    width: 40px;
    height: 40px;
    border-radius: var(--radius-md);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.25rem;
}

.admin-stat-value {
    font-size: 2rem;
    font-weight: 700;
    color: var(--gray-900);
    margin-bottom: var(--spacing-xs);
}

.admin-stat-change {
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: 0.25rem;
}

.admin-stat-change.positive {
    color: var(--success-600);
}

.admin-stat-change.negative {
    color: var(--danger-600);
}

/* ==========================================
    &  
   ========================================== */
.admin-error-message {
    background: linear-gradient(135deg, #FEE2E2 0%, #FECACA 100%);
    border-left: 4px solid var(--danger-600);
    color: var(--danger-600);
    padding: var(--spacing-md) var(--spacing-lg);
    border-radius: var(--radius-md);
    margin-bottom: var(--spacing-lg);
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
    animation: slideDown 0.3s ease;
}

.admin-loading-text {
    padding: var(--spacing-xl);
    text-align: center;
    font-size: 1.125rem;
    color: var(--gray-600);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: var(--spacing-md);
}

.admin-error-display-initial {
    padding: var(--spacing-xl);
    color: var(--danger-600);
    font-weight: 600;
    text-align: center;
    font-size: 1.125rem;
    background: white;
    border-radius: var(--radius-lg);
    box-shadow: var(--shadow-md);
}

/* ==========================================
    &  
   ========================================== */
.admin-controls-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--spacing-lg);
    flex-wrap: wrap;
    gap: var(--spacing-md);
}

.admin-toggle-button {
    padding: var(--spacing-sm) var(--spacing-lg);
    border: 2px solid var(--primary-600);
    border-radius: var(--radius-md);
    background: white;
    color: var(--primary-600);
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 0.875rem;
    display: flex;
    align-items: center;
    gap: var(--spacing-xs);
}

.admin-toggle-button:hover {
    background: var(--primary-50);
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
}

.admin-toggle-button.active {
    background: var(--primary-600);
    color: white;
}

.admin-search-section {
    display: flex;
    gap: var(--spacing-md);
    margin-bottom: var(--spacing-lg);
    align-items: stretch;
}

.admin-search-input {
    flex-grow: 1;
    padding: var(--spacing-sm) var(--spacing-lg);
    border: 2px solid var(--gray-200);
    border-radius: var(--radius-md);
    font-size: 0.9375rem;
    color: var(--gray-700);
    transition: all 0.3s ease;
    background: white;
}

.admin-search-input:focus {
    border-color: var(--primary-600);
    box-shadow: 0 0 0 3px var(--primary-50);
    outline: none;
}

.admin-search-input::placeholder {
    color: var(--gray-400);
}

/* ==========================================
    
   ========================================== */
.admin-table-container {
    background: white;
    border-radius: var(--radius-lg);
    box-shadow: var(--shadow-md);
    overflow: hidden;
    border: 1px solid var(--gray-200);
}

.admin-user-table {
    width: 100%;
    border-collapse: collapse;
}

.admin-table-header {
    background: linear-gradient(135deg, var(--gray-50) 0%, var(--gray-100) 100%);
}

.admin-table-header th {
    padding: var(--spacing-md) var(--spacing-lg);
    text-align: center;
    color: var(--gray-700);
    text-transform: uppercase;
    font-size: 0.75rem;
    font-weight: 700;
    letter-spacing: 0.05em;
    border-bottom: 2px solid var(--gray-300);
}

.admin-table-body {
    color: var(--gray-700);
    font-size: 0.875rem;
}

.admin-table-row {
    border-bottom: 1px solid var(--gray-200);
    transition: all 0.2s ease;
}

.admin-table-row:last-child {
    border-bottom: none;
}

.admin-table-row:hover {
    background: var(--gray-50);
    transform: scale(1.005);
}

.admin-table-cell {
    padding: var(--spacing-md) var(--spacing-lg);
    vertical-align: middle;
    text-align: center;
}

/* ==========================================
    &  
   ========================================== */
.admin-role-badge,
.admin-status-badge {
    padding: 0.375rem 0.75rem;
    font-size: 0.75rem;
    font-weight: 600;
    border-radius: 9999px;
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
    min-width: 70px;
    justify-content: center;
    text-transform: uppercase;
    letter-spacing: 0.025em;
}

.admin-role-badge.admin {
    background: linear-gradient(135deg, #DBEAFE 0%, #BFDBFE 100%);
    color: #1E40AF;
    border: 1px solid #93C5FD;
}

.admin-role-badge.user {
    background: linear-gradient(135deg, #D1FAE5 0%, #A7F3D0 100%);
    color: #065F46;
    border: 1px solid #6EE7B7;
}

.admin-status-badge.active {
    background: linear-gradient(135deg, #D1FAE5 0%, #A7F3D0 100%);
    color: #065F46;
    border: 1px solid #6EE7B7;
}

.admin-status-badge.inactive {
    background: linear-gradient(135deg, #FEE2E2 0%, #FECACA 100%);
    color: #991B1B;
    border: 1px solid #FCA5A5;
}

/* ==========================================
    
   ========================================== */
.admin-action-buttons {
    display: flex;
    justify-content: center;
    gap: var(--spacing-sm);
    flex-wrap: wrap;
}

.admin-action-button {
    color: white;
    padding: 0.5rem 1rem;
    border-radius: var(--radius-md);
    font-size: 0.8125rem;
    font-weight: 600;
    cursor: pointer;
    border: none;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
    display: inline-flex;
    align-items: center;
    gap: 0.375rem;
    text-transform: capitalize;
}

.admin-action-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
}

.admin-action-button:active {
    transform: translateY(0);
    box-shadow: var(--shadow-sm);
}

.admin-button-grant-admin {
    background: linear-gradient(135deg, var(--info-500) 0%, #2563EB 100%);
}

.admin-button-grant-admin:hover {
    background: linear-gradient(135deg, #2563EB 0%, #1D4ED8 100%);
}

.admin-button-revoke-admin {
    background: linear-gradient(135deg, var(--warning-500) 0%, var(--warning-600) 100%);
}

.admin-button-revoke-admin:hover {
    background: linear-gradient(135deg, var(--warning-600) 0%, #B45309 100%);
}

.admin-button-update-level,
.admin-button-update-flag {
    background: linear-gradient(135deg, var(--gray-500) 0%, var(--gray-600) 100%);
}

.admin-button-update-level:hover,
.admin-button-update-flag:hover {
    background: linear-gradient(135deg, var(--gray-600) 0%, var(--gray-700) 100%);
}

/* ==========================================
   
   ========================================== */
.admin-modal-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.6);
    backdrop-filter: blur(4px);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
}

.admin-modal-content {
    background: white;
    padding: var(--spacing-xl);
    border-radius: var(--radius-xl);
    box-shadow: var(--shadow-xl);
    width: 90%;
    max-width: 28rem;
    animation: slideUp 0.3s ease;
    border: 1px solid var(--gray-200);
}

.admin-modal-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: var(--spacing-lg);
    color: var(--gray-900);
    text-align: center;
}

.admin-form-group {
    margin-bottom: var(--spacing-lg);
}

.admin-form-label {
    display: block;
    color: var(--gray-700);
    font-size: 0.9375rem;
    font-weight: 600;
    margin-bottom: var(--spacing-sm);
}

.admin-form-input,
.admin-form-select {
    width: 100%;
    padding: var(--spacing-sm) var(--spacing-md);
    border: 2px solid var(--gray-200);
    border-radius: var(--radius-md);
    font-size: 1rem;
    color: var(--gray-700);
    transition: all 0.2s ease;
    background: white;
}

.admin-form-input:focus,
.admin-form-select:focus {
    border-color: var(--primary-600);
    box-shadow: 0 0 0 3px var(--primary-50);
    outline: none;
}

.admin-modal-actions {
    display: flex;
    justify-content: flex-end;
    gap: var(--spacing-md);
    margin-top: var(--spacing-xl);
}

.admin-modal-button {
    font-weight: 600;
    padding: var(--spacing-sm) var(--spacing-xl);
    border-radius: var(--radius-md);
    cursor: pointer;
    border: none;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
    font-size: 0.9375rem;
}

.admin-modal-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
}

.admin-modal-cancel-button {
    background: var(--gray-200);
    color: var(--gray-700);
}

.admin-modal-cancel-button:hover {
    background: var(--gray-300);
}

.admin-modal-save-button {
    background: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-700) 100%);
    color: white;
}

.admin-modal-save-button:hover {
    background: linear-gradient(135deg, var(--primary-700) 0%, #3730A3 100%);
}

/* ==========================================
   
   ========================================== */
.admin-pagination-controls {
    display: flex;
    justify-content: center;
    gap: var(--spacing-xs);
    margin-top: var(--spacing-xl);
    padding-bottom: var(--spacing-lg);
}

.admin-pagination-button {
    padding: var(--spacing-sm) var(--spacing-md);
    border: 2px solid var(--gray-200);
    border-radius: var(--radius-md);
    background: white;
    color: var(--gray-700);
    font-size: 0.875rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    min-width: 40px;
}

.admin-pagination-button:hover:not(:disabled) {
    background: var(--gray-50);
    border-color: var(--primary-600);
    color: var(--primary-600);
    transform: translateY(-2px);
    box-shadow: var(--shadow-sm);
}

.admin-pagination-button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    background: var(--gray-100);
}

.admin-pagination-button.active {
    background: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-700) 100%);
    color: white;
    border-color: var(--primary-600);
}

/* ==========================================
    
   ========================================== */
.tab-navigation {
    display: flex;
    border-bottom: 2px solid var(--gray-200);
    margin-bottom: var(--spacing-xl);
    background: white;
    border-radius: var(--radius-lg) var(--radius-lg) 0 0;
    padding: 0 var(--spacing-md);
    box-shadow: var(--shadow-sm);
}

.tab-button {
    background: none;
    border: none;
    padding: var(--spacing-md) var(--spacing-xl);
    cursor: pointer;
    font-size: 0.9375rem;
    font-weight: 600;
    color: var(--gray-600);
    border-bottom: 3px solid transparent;
    transition: all 0.3s ease;
    position: relative;
}

.tab-button::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--primary-600), var(--secondary-600));
    transform: scaleX(0);
    transition: transform 0.3s ease;
}

.tab-button:hover {
    color: var(--gray-900);
    background: var(--gray-50);
}

.tab-button.active {
    color: var(--primary-600);
}

.tab-button.active::after {
    transform: scaleX(1);
}

/* ==========================================
   HR   
   ========================================== */
.hr-permissions-container {
    padding: var(--spacing-lg) 0;
}

.hr-permission-form-section {
    background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
    border-radius: var(--radius-lg);
    padding: var(--spacing-xl);
    margin-bottom: var(--spacing-xl);
    border: 1px solid var(--gray-200);
    box-shadow: var(--shadow-sm);
}

.hr-permission-form-section h3 {
    margin-bottom: var(--spacing-lg);
    color: var(--gray-900);
    font-size: 1.25rem;
    font-weight: 700;
}

.hr-permission-form {
    display: flex;
    flex-direction: column;
    gap: var(--spacing-lg);
}

.form-row {
    display: flex;
    gap: var(--spacing-md);
    align-items: end;
    flex-wrap: wrap;
}

.form-group {
    display: flex;
    flex-direction: column;
    min-width: 200px;
    flex: 1;
}

.form-group label {
    margin-bottom: var(--spacing-xs);
    font-weight: 600;
    color: var(--gray-700);
    font-size: 0.875rem;
}

.form-select {
    padding: var(--spacing-sm) var(--spacing-md);
    border: 2px solid var(--gray-200);
    border-radius: var(--radius-md);
    font-size: 0.875rem;
    background: white;
    color: var(--gray-700);
    transition: all 0.2s ease;
}

.form-select:focus {
    outline: none;
    border-color: var(--primary-600);
    box-shadow: 0 0 0 3px var(--primary-50);
}

.submit-button {
    background: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-700) 100%);
    color: white;
    border: none;
    padding: var(--spacing-sm) var(--spacing-xl);
    border-radius: var(--radius-md);
    cursor: pointer;
    font-weight: 600;
    height: fit-content;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
}

.submit-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    background: linear-gradient(135deg, var(--primary-700) 0%, #3730A3 100%);
}

.hr-permissions-display {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
    gap: var(--spacing-xl);
}

.permissions-section {
    background: white;
    border-radius: var(--radius-lg);
    padding: var(--spacing-xl);
    box-shadow: var(--shadow-md);
    border: 1px solid var(--gray-200);
}

.permissions-section h4 {
    margin-bottom: var(--spacing-lg);
    color: var(--gray-900);
    font-size: 1.125rem;
    font-weight: 700;
}

.permissions-table-container {
    border: 1px solid var(--gray-200);
    border-radius: var(--radius-md);
    overflow: hidden;
}

.permissions-table {
    width: 100%;
    border-collapse: collapse;
}

.permissions-table th {
    background: var(--gray-100);
    padding: var(--spacing-md);
    text-align: left;
    font-weight: 700;
    font-size: 0.875rem;
    color: var(--gray-700);
    text-transform: uppercase;
    letter-spacing: 0.05em;
    border-bottom: 2px solid var(--gray-300);
}

.permissions-table td {
    padding: var(--spacing-md);
    border-bottom: 1px solid var(--gray-200);
    font-size: 0.875rem;
    color: var(--gray-700);
}

.permissions-table tbody tr {
    transition: background 0.2s ease;
}

.permissions-table tbody tr:hover {
    background: var(--gray-50);
}

.remove-permission-button {
    background: linear-gradient(135deg, var(--danger-500) 0%, var(--danger-600) 100%);
    color: white;
    border: none;
    padding: 0.375rem 0.75rem;
    border-radius: var(--radius-sm);
    cursor: pointer;
    font-size: 0.8125rem;
    font-weight: 600;
    transition: all 0.2s ease;
    box-shadow: var(--shadow-sm);
}

.remove-permission-button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
    background: linear-gradient(135deg, var(--danger-600) 0%, #B91C1C 100%);
}

.no-data {
    text-align: center;
    color: var(--gray-500);
    font-style: italic;
    padding: var(--spacing-xl);
}

/* ==========================================
   
   ========================================== */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes shimmer {
    0% {
        background-position: -1000px 0;
    }
    100% {
        background-position: 1000px 0;
    }
}

.loading-shimmer {
    animation: shimmer 2s infinite;
    background: linear-gradient(to right, var(--gray-100) 4%, var(--gray-200) 25%, var(--gray-100) 36%);
    background-size: 1000px 100%;
}

/* ==========================================
    
   ========================================== */
@media (max-width: 1024px) {
    .hr-permissions-display {
        grid-template-columns: 1fr;
    }

    .admin-stats-container {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }
}

@media (max-width: 768px) {
    .admin-dashboard-container {
        padding: var(--spacing-md);
    }

    .form-row {
        flex-direction: column;
        align-items: stretch;
    }

    .form-group {
        min-width: unset;
    }

    .admin-action-buttons {
        flex-direction: column;
    }

    .admin-action-button {
        width: 100%;
    }

    .admin-stats-container {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 640px) {
    .admin-search-section {
        flex-direction: column;
    }

    .admin-pagination-controls {
        flex-wrap: wrap;
    }

    .admin-pagination-button {
        flex-grow: 1;
    }

    .tab-navigation {
        overflow-x: auto;
    }
}

/* Header Area */
.admin-header-row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: var(--spacing-xl);
}

.admin-secondary-button {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.5rem 1rem;
    background: white;
    border: 1px solid var(--gray-300);
    border-radius: var(--radius-md);
    color: var(--gray-700);
    font-size: 0.875rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s;
}

.admin-secondary-button:hover {
    background: var(--gray-50);
    border-color: var(--gray-400);
}

/* User Table Enhancements */
.clickable-row {
    cursor: pointer;
}

.clickable-row:hover {
    background-color: var(--primary-50) !important;
}

.user-info-cell {
    display: flex;
    flex-direction: column;
    align-items:center;
}

.user-name {
    font-weight: 600;
    color: var(--gray-900);
}

.user-id {
    font-size: 0.75rem;
    color: var(--gray-500);
}

.badge-level {
    background: var(--gray-100);
    color: var(--gray-700);
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 0.75rem;
    font-weight: 600;
}

.badge-admin {
    background: #FEF3C7;
    color: #92400E;
    padding: 2px 8px;
    border-radius: 999px;
    font-size: 0.75rem;
    font-weight: 700;
}

.badge-user {
    background: #E5E7EB;
    color: #374151;
    padding: 2px 8px;
    border-radius: 999px;
    font-size: 0.75rem;
}

.status-dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin-right: 6px;
}
.status-dot.active { background-color: var(--success-500); }
.status-dot.inactive { background-color: var(--gray-400); }

.btn-manage {
    background: transparent;
    color: var(--primary-600);
    border: 1px solid var(--primary-600);
    padding: 4px 12px;
    border-radius: 4px;
    font-size: 0.75rem;
    cursor: pointer;
    transition: all 0.2s;
}

.btn-manage:hover {
    background: var(--primary-600);
    color: white;
}

/* ==========================================
   Drawer (Slide-over) Styles
   ========================================== */
.admin-drawer-overlay {
    position: fixed;
    inset: 0;
    background: rgba(0,0,0,0.4);
    backdrop-filter: blur(2px);
    z-index: 999;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.3s ease;
}

.admin-drawer-overlay.open {
    opacity: 1;
    pointer-events: auto;
}

.admin-drawer {
    position: fixed;
    top: 0;
    right: 0;
    width: 400px;
    height: 100vh;
    background: white;
    box-shadow: -4px 0 24px rgba(0,0,0,0.15);
    z-index: 1000;
    transform: translateX(100%);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    display: flex;
    flex-direction: column;
}

.admin-drawer.open {
    transform: translateX(0);
}

.drawer-header {
    padding: var(--spacing-lg);
    border-bottom: 1px solid var(--gray-200);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.drawer-header h2 {
    font-size: 1.125rem;
    font-weight: 700;
    color: var(--gray-900);
    margin: 0;
}

.btn-close {
    background: none;
    border: none;
    cursor: pointer;
    color: var(--gray-500);
    padding: 4px;
    border-radius: 4px;
}
.btn-close:hover { background: var(--gray-100); color: var(--gray-900); }

.drawer-body {
    flex: 1;
    overflow-y: auto;
    padding: var(--spacing-lg);
}

.drawer-section {
    margin-bottom: var(--spacing-xl);
}

.ad-profile-section {
    display: flex;
    align-items: center;
    gap: var(--spacing-md);
    background: var(--gray-50);
    padding: var(--spacing-md);
    border-radius: var(--radius-lg);
}

.profile-avatar-placeholder {
    width: 48px;
    height: 48px;
    background: var(--primary-600);
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.25rem;
    font-weight: 700;
}

.profile-details h3 {
    margin: 0 0 4px 0;
    font-size: 1rem;
    color: var(--gray-900);
}
.profile-details p {
    margin: 0;
    color: var(--gray-500);
    font-size: 0.875rem;
}

.section-title {
    font-size: 0.875rem;
    text-transform: uppercase;
    color: var(--gray-500);
    margin-bottom: var(--spacing-md);
    letter-spacing: 0.05em;
}

.section-desc {
    font-size: 0.8rem;
    color: var(--gray-500);
    margin-bottom: var(--spacing-md);
}

.input-with-button {
    display: flex;
    gap: 8px;
}
.input-with-button input {
    flex: 1;
    padding: 6px 10px;
    border: 1px solid var(--gray-300);
    border-radius: 4px;
}
.btn-save-mini {
    padding: 0 12px;
    background: var(--gray-800);
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 0.8rem;
}

.drawer-divider {
    height: 1px;
    background: var(--gray-200);
    margin: var(--spacing-xl) 0;
}

/* Permission List & Toggle Switch */
.permission-list {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.permission-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    border: 1px solid var(--gray-100);
    border-radius: 6px;
    transition: background 0.2s;
}

.permission-item:hover {
    background: var(--gray-50);
}

.perm-info {
    display: flex;
    flex-direction: column;
}
.perm-name {
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--gray-800);
}
.perm-code {
    font-size: 0.7rem;
    color: var(--gray-400);
}

/* Toggle Switch CSS */
.toggle-switch {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 24px;
}
.toggle-switch input { opacity: 0; width: 0; height: 0; }
.slider {
    position: absolute;
    cursor: pointer;
    top: 0; left: 0; right: 0; bottom: 0;
    background-color: #ccc;
    transition: .4s;
    border-radius: 24px;
}
.slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 3px;
    bottom: 3px;
    background-color: white;
    transition: .4s;
    border-radius: 50%;
}
input:checked + .slider { background-color: var(--primary-600); }
input:checked + .slider:before { transform: translateX(20px); }

/* ==========================================
   Password Reset Section (Danger Zone)
   ========================================== */
/* 1.  :    ' '  */
.password-reset-zone {
    margin-top: var(--spacing-xl);
    padding: var(--spacing-lg);
    background-color: #FEF2F2;
    border: 1px solid #FECACA;
    border-radius: var(--radius-md);
    transition: all 0.3s ease;
}

/* ()      */
.password-reset-zone.active {
    background-color: #FFF1F2;
    border-color: #FDA4AF;
    box-shadow: 0 4px 6px -1px rgba(220, 38, 38, 0.1);
}

/* 2.   ( + / ) */
.password-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.password-label {
    font-size: 0.875rem;
    font-weight: 700;
    color: #991B1B;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

/*    (/) */
.btn-toggle-danger {
    padding: 0.375rem 0.75rem;
    font-size: 0.75rem;
    font-weight: 600;
    border-radius: var(--radius-md);
    cursor: pointer;
    border: 1px solid #FECACA;
    background-color: white;
    color: #EF4444;
    transition: all 0.2s;
}

.btn-toggle-danger:hover {
    background-color: #EF4444;
    color: white;
}

.btn-toggle-danger.cancel {
    background-color: #9CA3AF;
    border-color: #9CA3AF;
    color: white;
}

/* 3.    */
.password-form-container {
    margin-top: var(--spacing-md);
    padding-top: var(--spacing-md);
    border-top: 1px dashed #FECACA;
    animation: slideDown 0.3s ease forwards; /*    */
}

/* 4.    (  ) */
.password-input,
.btn-submit-danger {
    width: 100%;                  /*  100%  */
    height: 42px;                 /*   () */
    box-sizing: border-box;       /*     () */
    border-radius: 6px;
    font-size: 0.875rem;
    display: block;
}

/*    */
.password-input {
    padding: 0 12px;
    background-color: #ffffff;
    border: 1px solid #D1D5DB;
    color: #111827;
    margin-bottom: 8px;           /*   */
    transition: all 0.2s ease;
}

.password-input:focus {
    outline: none;
    border-color: #EF4444;
    box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

/*     */
.btn-submit-danger {
    background: #EF4444;
    color: #ffffff;
    border: none;
    font-weight: 600;
    cursor: pointer;
    transition: background 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-submit-danger:hover {
    background: #DC2626;
}

.btn-submit-danger:active {
    transform: scale(0.98);
}

/* 5.     */
.password-warning {
    margin-top: 12px;
    padding: 10px;
    background-color: #FFF5F5;
    border-radius: 6px;
    font-size: 0.75rem;
    color: #C53030;
    display: flex;
    gap: 8px;
    line-height: 1.5;
}

/*   */
@keyframes slideDown {
    from { opacity: 0; transform: translateY(-5px); }
    to { opacity: 1; transform: translateY(0); }
}
/* Responsive Drawer */
@media (max-width: 480px) {
    .admin-drawer { width: 100%; }
}