/*    */
.leave-attachments {
    font-family: inherit;
    font-size: 14px;
    color: #222;
    text-align: left;
    margin-bottom: 40px;
}

/*   */
.leave-attachments label {
    display: block;
    margin-bottom: 4px;
    font-weight: 500;
    text-align: left;
}

/*  input  */
.leave-attachments > div {
    text-align: left;
}

/*  input -    */
.upload-input {
    display: inline-block;
    margin: 4px 0;
    font-size: 12px;
    padding: 4px 8px;
    border: 1px solid #d1d5da;
    border-radius: 4px;
    background: #f8f9fa;
    max-width: 250px;
}

.upload-input::-webkit-file-upload-button {
    background: #f1f3f4;
    border: 1px solid #dadce0;
    border-radius: 3px;
    padding: 4px 8px;
    margin-right: 8px;
    font-size: 11px;
    cursor: pointer;
}

.upload-input::-webkit-file-upload-button:hover {
    background: #e8eaed;
}

/*    */
.uploading {
    color: #666;
    font-size: 12px;
    margin-top: 4px;
}

/*   */
.attach-error {
    color: #c9302c;
    font-size: 12px;
    margin-top: 4px;
}

/*    */
.attachment-list {
    padding: 0;
    margin: 6px 0;
    list-style: none;
}

/*   -   */
.attachment-item {
    display: flex;
    gap: 8px;
    align-items: center;
    padding: 6px 0;
    border-bottom: 1px dashed #eee;
}

/*   ( ) */
.file-link {
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    color: #0366d6;
    text-decoration: underline;
    cursor: pointer;
    font-weight: 500;
    font-size: 13px;
    text-align: left;
    max-width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.file-link:hover:not(:disabled) {
    color: #0256cc;
}

/*   */
.file-link:disabled,
.file-link[disabled] {
    color: #999;
    cursor: default;
    text-decoration: none;
}

/*    -    */
.attachment-size {
    color: #888;
    font-size: 11px;
    flex-shrink: 0;
}

/*    -   */
.attachment-button {
    background: #f6f8fa;
    border: 1px solid #d1d5da;
    color: #24292e;
    padding: 4px 8px;
    border-radius: 3px;
    cursor: pointer;
    font-size: 11px;
    min-width: 40px;
    flex-shrink: 0;
}

/*   ( ) -   */
.attachment-button.btn-delete {
    background: #fff5f5;
    border: 1px solid #f0a3a3;
    color: #c9302c;
}

/*  hover */
.attachment-button:hover:not(:disabled) {
    filter: brightness(0.95);
}

/*  disabled  */
.attachment-button:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

/*    */
.attachment-empty {
    color: #666;
    font-size: 12px;
    padding: 4px 0;
    font-style: italic;
}