:root {
    /*  /   */
    --clean-50: #f0f4f8;
    --clean-100: #e1e8ed;
    --clean-200: #c7d2dd;
    --clean-300: #9fb3c8;
    --clean-400: #8a9fb5;

    /*   */
    --neutral-50: #f7f8fa;
    --neutral-100: #e8ebf0;
    --neutral-200: #d1d6dd;
    --neutral-300: #b4bcc8;
    --neutral-400: #8b95a6;
    --neutral-500: #6b7785;
    --neutral-600: #545d6a;
    --neutral-700: #3d4551;
    --neutral-800: #2a303c;
    --neutral-900: #1a1f2e;

    /*   */
    --blue-50: #eff6ff;
    --blue-100: #dbeafe;
    --blue-200: #bfdbfe;
    --blue-300: #93c5fd;
    --blue-400: #60a5fa;
    --blue-500: #3b82f6;
    --blue-600: #2563eb;
    --blue-700: #1d4ed8;

    /*  */
    --green-500: #10b981;
    --green-600: #059669;
    --green-700: #047857;

    /*  */
    --red-500: #ef4444;
    --red-600: #dc2626;
    --red-700: #b91c1c;

    /*  */
    --shadow-soft: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
    --shadow-medium: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
    --shadow-large: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(42, 48, 60, 0.7);
    backdrop-filter: blur(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    animation: fadeIn 0.3s ease;
}

/*     */
.signature-mode-tabs {
    padding: 0 24px;
    border-bottom: 1px solid #e2e8f0;  /* 2px -> 1px */
    margin-bottom: 0;  /* 20px -> 0 */
}

/* /  -   */
.modal-signature-canvas {
    padding: 24px;
    background: #f8fafc;
    margin-bottom: 0;  /* 20px -> 0 */
    display: flex;
    justify-content: center;
}

.signature-mode-tabs button,
.signature-mode-tabs .tab-btn {
    flex: 1;
    padding: 12px 20px;
    background: none;
    border: none;
    border-bottom: 3px solid transparent;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    color: #666;
    transition: all 0.3s ease;
}

.signature-mode-tabs button:hover,
.signature-mode-tabs .tab-btn:hover {
    color: #333;
    background-color: #f5f5f5;
}

.signature-mode-tabs button.active,
.signature-mode-tabs .tab-btn.active {
    color: #1976d2;
    border-bottom-color: #1976d2;
    font-weight: 600;
}

/*   */
.upload-section {
    min-height: 200px;
    border: 2px dashed #e2e8f0;
    background-color: white;
}

/*     */
.upload-section .upload-btn,
.upload-section .btn {
    padding: 12px 24px;
    font-size: 14px;
    font-weight: 500;
    border-radius: 8px;  /* 6px -> 8px () */
    cursor: pointer;
    transition: all 0.2s ease;
    background: white;  /*  */
    color: #0f172a;  /*  */
    border: 1px solid #e2e8f0;  /*  */
}

.upload-section .upload-btn:hover,
.upload-section .btn:hover {
    background: #f8fafc;  /*  */
}

/*   */
.image-preview {
    margin-top: 20px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: white;
    max-width: 400px;
}

.image-preview img {
    width: 100%;
    height: auto;
    max-height: 200px;
    object-fit: contain;
    display: block;
}

/*     */

.sigCanvas,
.signature-canvas {
    border: 1px solid #e2e8f0;
    border-radius: 8px;
    background: white;
}

/*   */
.error-message {
    color: #ef4444;
    font-size: 13px;
    padding: 0;  /* 8px 16px  */
    background: none;  /*   */
    border: none;
    margin: 10px 0;
    text-align: center;
}

.modal-footer {
    padding: 16px 24px;
    border-top: 1px solid #e2e8f0;
    margin-top: 0;  /* 20px -> 0 */
}

/*    */
.signature-actions,
.modal-footer {
    gap: 8px;
}

.signature-actions button,
.modal-footer button {
    height: 40px;
    padding: 0 16px;
    border-radius: 8px;  /* 10px -> 8px */
    font-size: 14px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);  /* var(--shadow-soft)    */
}

.signature-actions button:hover,
.modal-footer .btn-primary:hover {
    background: #1d4ed8;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.signature-actions button:first-child,
.modal-footer .btn-secondary {
    background: white;
    color: #0f172a;
    border: 1px solid #e2e8f0;
}

.signature-actions button:first-child:hover,
.modal-footer .btn-secondary:hover {
    background: #f8fafc;
}

.signature-actions button:nth-child(2),
.modal-footer .btn-primary {
    background-color: #1976d2;
    color: white;
}

.signature-actions button:nth-child(2):hover,
.modal-footer .btn-primary:hover {
    background-color: #1565c0;
}

.signature-actions button:last-child {
    background-color: #757575;
    color: white;
}

.signature-actions button:last-child:hover {
    background-color: #616161;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.popup-content {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(20px);
    padding: 32px;
    border-radius: 20px;
    box-shadow: var(--shadow-xl);
    width: 90%;
    max-width: 550px;
    text-align: center;
    max-height: 90vh;
    overflow-y: auto;
    border: 1px solid var(--clean-200);
    position: relative;
    animation: slideUp 0.3s ease;
}

@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(30px) scale(0.95);
    }
    to {
        opacity: 1;
        transform: translateY(0) scale(1);
    }
}

.popup-content::-webkit-scrollbar {
    width: 6px;
}

.popup-content::-webkit-scrollbar-track {
    background: var(--clean-100);
    border-radius: 3px;
}

.popup-content::-webkit-scrollbar-thumb {
    background: var(--clean-300);
    border-radius: 3px;
}

.popup-content::-webkit-scrollbar-thumb:hover {
    background: var(--clean-400);
}

.popup-content h2 {
    color: var(--neutral-800);
    margin-bottom: 16px;
    font-size: 24px;
    font-weight: 700;
    background: linear-gradient(135deg, var(--blue-600) 0%, var(--clean-400) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.popup-content h3 {
    color: var(--neutral-700);
    margin-bottom: 16px;
    font-size: 18px;
    font-weight: 600;
    position: relative;
    padding-bottom: 8px;
}

.popup-content h3::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    width: 40px;
    height: 2px;
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--clean-400) 100%);
    border-radius: 1px;
}

.popup-content > p {
    color: var(--neutral-600);
    margin-bottom: 24px;
    font-size: 15px;
    line-height: 1.5;
    font-weight: 500;
}

.form-group {
    margin-bottom: 20px;
    text-align: left;
}

.form-group label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: var(--neutral-700);
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.form-group input[type="text"],
.form-group input[type="password"] {
    width: 100%;
    padding: 12px 16px;
    border: 2px solid var(--clean-200);
    border-radius: 12px;
    font-size: 15px;
    font-weight: 500;
    color: var(--neutral-700);
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(10px);
    transition: all 0.3s ease;
    outline: none;
    box-sizing: border-box;
}

.form-group input[type="text"]:focus,
.form-group input[type="password"]:focus {
    border-color: var(--blue-500);
    box-shadow: 0 0 0 4px rgba(59, 130, 246, 0.1);
    background: rgba(255, 255, 255, 0.95);
    transform: translateY(-1px);
}

.form-group input[type="text"]::placeholder,
.form-group input[type="password"]::placeholder {
    color: var(--neutral-400);
    font-weight: 400;
}

.password-change-section {
    border-top: 2px solid var(--clean-200);
    padding-top: 24px;
    margin-top: 24px;
    border-bottom: 2px solid var(--clean-200);
    padding-bottom: 24px;
    margin-bottom: 24px;
    background: linear-gradient(135deg, var(--clean-50) 0%, rgba(255, 255, 255, 0.5) 100%);
    border-radius: 16px;
    padding: 24px;
    border: 1px solid var(--clean-200);
}

.password-change-section h3 {
    color: var(--neutral-800);
    text-align: center;
}

.popup-actions {
    margin-top: 32px;
    display: flex;
    justify-content: center;
    gap: 16px;
    flex-wrap: wrap;
}

.popup-actions button {
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--blue-600) 100%);
    color: white;
    border: none;
    padding: 12px 28px;
    border-radius: 12px;
    cursor: pointer;
    font-size: 15px;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: var(--shadow-soft);
    position: relative;
    overflow: hidden;
}

.popup-actions button::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
}

.popup-actions button:hover::before {
    left: 100%;
}

.popup-actions button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-medium);
}

.popup-actions button:nth-child(2) {
    background: linear-gradient(135deg, var(--neutral-500) 0%, var(--neutral-600) 100%);
}

.popup-actions button:disabled {
    background: linear-gradient(135deg, var(--neutral-300) 0%, var(--neutral-400) 100%);
    cursor: not-allowed;
    transform: none;
    box-shadow: var(--shadow-soft);
}

.popup-actions button:disabled::before {
    display: none;
}

/*    */
.signature-btn {
    background: linear-gradient(135deg, var(--green-500) 0%, var(--green-600) 100%);
    color: white;
    border: none;
    padding: 12px 24px;
    border-radius: 12px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 12px;
    box-shadow: var(--shadow-soft);
    position: relative;
    overflow: hidden;
}

.signature-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
    transition: left 0.5s;
}

.signature-btn:hover::before {
    left: 100%;
}

.signature-btn:hover {
    background: linear-gradient(135deg, var(--green-600) 0%, var(--green-700) 100%);
    transform: translateY(-2px);
    box-shadow: var(--shadow-medium);
}

/*   */
.signature-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 480px;
    max-width: 90%;
    background: white;  /* backdrop-filter  */
    transform: translate(-50%, -50%);
    border-radius: 16px;  /* 20px -> 16px */
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1);  /* var(--shadow-xl)    */
    padding: 0;  /* 28px -> 0 (  ) */
    z-index: 1100;
    border: 1px solid #e2e8f0;  /* var(--clean-200)    */
    overflow: hidden;  /*  */
}
.signature-modal-header {
    padding: 16px 24px;
    border-bottom: 1px solid #e2e8f0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


@keyframes modalSlideUp {
    from {
        opacity: 0;
        transform: translate(-50%, -50%) scale(0.9);
    }
    to {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }
}

/*   */
.sigCanvas {
    border: 3px solid var(--clean-200);
    border-radius: 12px;
    background: white;
    box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.06);
    transition: all 0.3s ease;
}

.sigCanvas:hover,
.signature-canvas:hover {
    border-color: #e2e8f0;  /* hover   */
    box-shadow: none;
}


/*    */
.signature-modal-header h3 {
    margin: 0;
    font-size: 18px;
    color: #0f172a;
    background: none;  /*   */
    -webkit-background-clip: unset;
    -webkit-text-fill-color: unset;
}

.signature-modal-header h3::after {
    display: none;  /*   */
}

.signature-modal-close-btn {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    color: #64748b;
    padding: 0;
    width: 24px;
    height: 24px;
}

/*      */
.signature-actions {
    display: flex;
    justify-content: center;
    gap: 12px;
    margin-top: 20px;
    flex-wrap: wrap;
}

/*     */
.signature-actions button {
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--blue-600) 100%);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 10px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: var(--shadow-soft);
    position: relative;
    overflow: hidden;
}

.signature-actions button::before,
.modal-footer button::before {
    display: none;
}

.signature-actions button:hover::before {
    left: 100%;
}

.signature-actions button:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-medium);
}

/*   (  ) */
.signature-actions button:first-child {
    background: linear-gradient(135deg, var(--neutral-500) 0%, var(--neutral-600) 100%);
}

.signature-actions button:first-child:hover {
    background: linear-gradient(135deg, var(--neutral-600) 0%, var(--neutral-700) 100%);
}

/*   ( ) */
.signature-actions button:last-child {
    background: linear-gradient(135deg, var(--red-500) 0%, var(--red-600) 100%);
}

.signature-actions button:last-child:hover {
    background: linear-gradient(135deg, var(--red-600) 0%, var(--red-700) 100%);
}


/*    */
.signature-modal .error-message {
    color: var(--red-600);
    margin-top: 12px;
    font-size: 14px;
    text-align: center;
    padding: 8px 16px;
    background: linear-gradient(135deg, rgba(239, 68, 68, 0.1) 0%, rgba(255, 255, 255, 0.8) 100%);
    border: 1px solid rgba(239, 68, 68, 0.2);
    border-radius: 8px;
}

.address-search-btn {
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--blue-600) 100%);
    color: white;
    border: none;
    padding: 10px 18px; /*   input   */
    border-radius: 10px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 600;
    transition: all 0.3s ease;
    box-shadow: var(--shadow-soft);
    position: relative;
    overflow: hidden;
    flex-shrink: 0; /* flex      */
}

.address-search-btn:hover {
    background: linear-gradient(135deg, var(--blue-600) 0%, var(--blue-700) 100%);
    transform: translateY(-1px);
    box-shadow: var(--shadow-medium);
}

/*        */
.profile-field .field-value > div.address-input-wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
}

.profile-field .field-value > div.address-input-wrapper input {
    flex: 1;
}

/*   */
@media (max-width: 768px) {
    .popup-content {
        width: 95%;
        padding: 24px;
        border-radius: 16px;
        margin: 20px;
    }

    .popup-content h2 {
        font-size: 20px;
    }

    .popup-content h3 {
        font-size: 16px;
    }

    .form-group {
        margin-bottom: 16px;
    }

    .form-group label {
        font-size: 13px;
    }

    .form-group input[type="text"],
    .form-group input[type="password"] {
        padding: 10px 14px;
        font-size: 14px;
    }

    .password-change-section {
        padding: 20px;
        margin: 20px 0;
    }

    .popup-actions {
        gap: 12px;
        margin-top: 24px;
    }

    .popup-actions button {
        padding: 10px 20px;
        font-size: 14px;
    }

    .signature-modal {
        width: 95%;
        padding: 24px;
        border-radius: 16px;
    }

    .signature-actions {
        gap: 8px;
    }

    .signature-actions button {
        padding: 8px 16px;
        font-size: 13px;
    }

    .sigCanvas {
        max-width: 100%;
    }
}

/* SMS    */
.verify-btn {
    padding: 8px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    white-space: nowrap;
}

.verify-btn:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

.verified-text {
    font-size: 12px;
    font-weight: bold;
    white-space: nowrap;
}

.timer-text {
    font-size: 12px;
    color: #666;
    margin-top: 5px;
}

/*    */
.consent-section {
    background: #fafafa;
    border-radius: 5px;
    border: 1px solid #ddd;
}

.consent-item {
    margin-bottom: 15px;
}

/*   (    ) */
.marketing-consent-field {
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    background: #f8f9fa;
    padding: 8px;
}
.marketing-consent-container {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-wrap: nowrap; /*    */
}

/*    */
.marketing-consent-label {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    cursor: pointer;
    font-size: 0.9em;
    flex: 0 1 auto;
}
.marketing-consent-checkbox {
    width: 18px;
    height: 18px;
    cursor: pointer;
}

/* ---   (address-search-btn  ) --- */
.detail-btn {
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--blue-600) 100%);
    color: white;
    border: none;
    padding: 8px 12px;     /*       */
    height: 34px;          /* input    ( ) */
    border-radius: 10px;
    cursor: pointer;
    font-size: 12px;
    font-weight: 600;
    transition: all 0.18s ease;
    box-shadow: var(--shadow-soft, 0 4px 8px rgba(0,0,0,0.08));
    position: relative;
    overflow: hidden;
    flex-shrink: 0;        /* flex   */
    white-space: nowrap;
    display: inline-flex;
    align-items: center;
    justify-content: center;
}

/* hover / focus  */
.detail-btn:hover,
.detail-btn:focus {
    background: linear-gradient(135deg, var(--blue-600) 0%, var(--blue-700) 100%);
    transform: translateY(-1px);
    box-shadow: var(--shadow-medium, 0 8px 20px rgba(0,0,0,0.12));
    outline: none; /* focus      */
}

/*     */
.policy-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
}

.policy-modal-content {
    background: white;
    border-radius: 10px;
    width: 95%;
    max-width: 900px;
    max-height: 85vh;
    overflow-y: auto;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.policy-modal-header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 15px 20px;
    border-bottom: 2px solid #f0f0f0;
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--clean-400) 100%);
    color: white;
    border-radius: 10px 10px 0 0;
}

.policy-modal-close-btn {
    background: none;
    border: none;
    font-size: 24px;
    cursor: pointer;
    color: white;
    padding: 0;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    transition: background-color 0.2s;
}

.policy-modal-close-btn:hover {
    background-color: rgba(255, 255, 255, 0.2);
}

.policy-modal-body {
    padding: 20px;
    line-height: 1.6;
}

/*    */
.policy-section {
    margin-bottom: 20px;
    padding: 15px;
    background: #f8f9fa;
    border-left: 4px solid #007bff;
    border-radius: 0 5px 5px 0;
}

.policy-section h3 {
    color: #007bff;
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 1.1em;
}

.policy-section h4 {
    color: #007bff;
    margin-bottom: 8px;
    font-size: 1em;
}

.policy-table {
    width: 100%;
    border-collapse: collapse;
    margin: 10px 0;
}

.policy-table th,
.policy-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
    font-size: 0.9em;
}

.policy-table th {
    background: #007bff;
    color: white;
    font-weight: bold;
}

.policy-table tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}

.policy-highlight {
    background: #fff3cd;
    padding: 10px;
    border-radius: 5px;
    margin: 10px 0;
    border-left: 3px solid #ffc107;
}

.policy-contact {
    background: #e9ecef;
    padding: 15px;
    border-radius: 5px;
    margin-top: 15px;
}

.policy-contact h3 {
    color: #495057;
    margin-top: 0;
    margin-bottom: 10px;
}

.policy-contact p {
    margin: 5px 0;
    font-size: 0.9em;
}

/*    */
.policy-marketing-benefit {
    background: linear-gradient(135deg, var(--blue-500) 0%, var(--clean-400) 100%);
    color: white;
    padding: 15px;
    border-radius: 8px;
    margin-bottom: 20px;
}

.policy-marketing-benefit h3 {
    margin-top: 0;
    color: white;
}

.policy-marketing-benefit ul {
    margin: 10px 0 0 0;
    padding-left: 20px;
}

.policy-marketing-benefit li {
    margin-bottom: 8px;
}

.policy-marketing-warning {
    background: #fff3cd;
    border: 1px solid #ffeaa7;
    padding: 10px;
    border-radius: 5px;
    margin: 10px 0;
    border-left: 3px solid #ffc107;
}

/*    */
@media (max-width: 768px) {
    .policy-modal-content {
        width: 95%;
        max-height: 90vh;
    }

    .policy-modal-header {
        padding: 15px;
    }

    .policy-modal-body {
        padding: 15px;
    }

    .policy-section {
        padding: 10px;
    }

    .policy-table {
        font-size: 0.8em;
    }

    .policy-table th,
    .policy-table td {
        padding: 6px;
    }
}