/* Header Styles - Enterprise Standard */
:root {
    --header-height: 64px; /*     ( ) */
    --header-bg: #ffffff;
    --border-color: #e5e7eb;
    --text-main: #111827;
    --text-sub: #6b7280;
    --accent-blue: #2563eb;
    --bg-hover: #f3f4f6;
}

.header {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: var(--header-height);
    background-color: var(--header-bg);
    border-bottom: 1px solid var(--border-color);
    z-index: 1000;
    /*       */
    backdrop-filter: blur(8px);
    background-color: rgba(255, 255, 255, 0.8);
}

.contents {
    display: flex;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between; /*    */
    padding: 0 20px;
}

/*  :  +  */
.header-left {
    display: flex;
    align-items: center;
    gap: 16px;
}

/*    */
.menu-toggle-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 8px;
    border: none;
    background: transparent;
    color: var(--text-sub);
    cursor: pointer;
    transition: all 0.2s;
}

.menu-toggle-btn:hover {
    background-color: var(--bg-hover);
    color: var(--accent-blue);
}

.logo-area {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}

.hospital-image {
    width: 32px; /*      */
    height: 32px;
    object-fit: contain;
}

.image-text {
    font-size: 18px; /*     */
    color: var(--text-main);
    font-weight: 700;
    letter-spacing: -0.02em;
}

/*  :     ( ) */
.header-right {
    display: flex;
    align-items: center;
    gap: 12px;
}

.system-badge {
    padding: 4px 10px;
    background-color: #eff6ff;
    color: #2563eb;
    border-radius: 999px;
    font-size: 12px;
    font-weight: 600;
}